input[type=radio] {
  display: none;
}
input[type=radio] + span {
  position: relative;
  padding-left: 40px;
  padding-top: 2px;
  padding-bottom: 2px;
  display: inline-block;
  cursor: pointer;
}
input[type=radio] + span:before {
  border: 2px solid #1D1D1F;
  content: " ";
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  top: 0;
  border-radius: 50%;
  box-sizing: border-box;
}
input[type=radio] + span:after {
  content: " ";
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  top: 0;
  opacity: 1;
  background: resolve("icon-check-white.svg") no-repeat center #059F8E;
  border-radius: 50%;
  box-sizing: border-box;
  border: 2px solid #059F8E;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
input[type=radio]:checked + span:after {
  opacity: 1;
}

input[type=checkbox] {
  display: none;
}
input[type=checkbox] + span {
  position: relative;
  padding-left: 40px;
  padding-bottom: 2px;
  display: inline-block;
  cursor: pointer;
}
input[type=checkbox] + span:before {
  border: 2px solid #1D1D1F;
  content: " ";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0;
  top: 1px;
  box-sizing: border-box;
  border-radius: 3px;
}
input[type=checkbox] + span:after {
  content: " ";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 0;
  top: 1px;
  opacity: 1;
  background: resolve("icon-check-white.svg") no-repeat center #059F8E;
  box-sizing: border-box;
  border: 2px solid #059F8E;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 3px;
}
input[type=checkbox]:checked + span:after {
  opacity: 1;
}

