  transition: all 0.3s ease-in-out;
  border-radius: 6px;
  margin-left: 30px;
}
.menu-link:before {
  width: 16px;
  left: 50%;
  margin-left: -8px;
  top: 50%;
  margin-top: -6px;
  height: 2px;
  background: #ffffff;
  display: block;
  content: " ";
  position: absolute;
}
.menu-link:after {
  width: 16px;
  left: 50%;
  margin-left: -8px;
  top: 50%;
  margin-top: 4px;
  height: 2px;
  background: #ffffff;
  display: block;
  content: " ";
  position: absolute;
}
.menu-link span:before {
  width: 16px;
  left: 50%;
  margin-left: -8px;
  top: 50%;
  margin-top: -1px;
  height: 2px;
  background: #ffffff;
  display: block;
  content: " ";
  position: absolute;
}
