  padding-top: 0px;
  display: inline-block;
  font-size: 16px;
  line-height: 1.6;
}
.wpcf7-checkbox > span input[type=checkbox] + span:before,
.wpcf7-acceptance > span input[type=checkbox] + span:before {
  background: #fff;
  border-radius: 3px;
  width: 18px;
  height: 18px;
  content: " ";
  position: absolute;
  left: 3px;
  top: 3px;
  box-shadow: 0px 0px 3px 0px rgba(66, 68, 90, 0.4);
